.header-container {
  height: 52px;
  background-color: #070707;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

ul, ul li {
  list-style: none;
}

a, a:link, a:visited, a:hover {
  text-decoration: none;
}

.header-list {
  gap: 50px;
  display: flex;
}

.logo-flame {
  width: 35px;
  height: 45.5px;
  fill: #bd00ff;
  margin-left: 10px;
  margin-right: 10px;
  transition: fill .3s;
}

.logo-header {
  max-height: 52px;
  align-items: center;
  display: flex;
}

.header-button-signin {
  width: 123px;
  height: 42px;
  color: #fff;
  letter-spacing: .02em;
  background: none;
  border: 2px solid #bd00ff;
  border-radius: 5px;
  margin-right: 40px;
  padding: 0;
  font-family: Roboto;
  font-size: 18px;
  transition: background-color .3s ease-in-out;
}

.header-button-signin:hover {
  background-color: #bd00ff;
}

.header-button-signin:focus {
  background-color: #bd00ff;
  outline: none;
}

.header-button-signup {
  width: 123px;
  height: 42px;
  color: #fff;
  letter-spacing: .02em;
  background: #bd00ff;
  border: 2px solid #bd00ff;
  border-radius: 5px;
  margin-right: 26px;
  padding: 0;
  font-family: Roboto;
  font-size: 18px;
  transition: background-color .3s ease-in-out;
}

.header-button-signup:hover {
  background-color: rgba(0, 0, 0, 0);
}

.header-button-signup:focus {
  background-color: rgba(0, 0, 0, 0);
  outline: none;
}

.header-li {
  color: #fff;
  letter-spacing: .02em;
  font-family: Roboto;
  font-size: 24px;
  text-decoration: none;
  transition: color .3s;
}

.header-li:focus, .header-li:hover {
  color: #ad2b89;
}

.header-logo-text {
  color: #fff;
  letter-spacing: .02em;
  font-family: Aubrey;
  font-size: 48px;
  transition: color .3s;
}

.logo-header:focus .header-logo-text, .logo-header:focus .logo-flame, .logo-header:hover .header-logo-text, .logo-header:hover .logo-flame, .header-logo-text:focus, .header-logo-text:hover, .logo-flame:focus, .logo-flame:hover {
  color: #bd00ff;
  fill: #fff;
}

.search-header {
  height: 35px;
  width: 100%;
  max-width: 243px;
  color: #fff;
  background: #302d2d;
  border: none;
  border-radius: 5px;
  outline: none;
  padding-left: 20px;
  font-size: 20px;
}

header {
  background-color: #4e4e4e;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: flex;
}

.search-container {
  align-items: center;
  display: flex;
  position: relative;
}

.search-input {
  width: 200px;
  color: #fff;
  background-color: #302d2d;
  border: none;
  border-radius: 7px;
  outline: none;
  padding: 10px;
  transition: width .3s ease-in-out;
}

.search-input::-ms-input-placeholder {
  color: #fff;
  letter-spacing: .02em;
  font-family: Roboto;
  font-size: 14px;
}

.search-input::placeholder {
  color: #fff;
  letter-spacing: .02em;
  font-family: Roboto;
  font-size: 14px;
}

.search-icon {
  width: 19px;
  height: 19px;
  cursor: pointer;
  fill: #fff;
  transition: color .3s ease-in-out;
  position: absolute;
  right: 10px;
}

.menu-toggle {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
}

.menu-container {
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: #000103;
  background-image: linear-gradient(206deg, #000103 0%, #5c489b 69%, #592689 91%);
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.modal-icon {
  fill: #bd00ff;
  color: #ad2b89;
  width: 32px;
  height: 32px;
  margin-right: 30px;
}

@media (max-width: 1024px) {
  .header-buttons, .search-container, .header-list {
    display: none;
  }

  .header-container {
    max-width: 100%;
    width: 100%;
  }
}

@media (min-width: 1025px) {
  .menu-container, .menu-toggle, .mobile-container {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .header-container {
    height: 60px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

